import React, { useEffect, useRef } from "react";
import mermaid from "mermaid";

const MermaidERD = () => {
  const mermaidRef = useRef(null);

  useEffect(() => {
    //   

    const timer = setTimeout(() => {
      if (!mermaidRef.current) {
        return;
      }

      try {
        // Mermaid 
        mermaid.initialize({
          theme: "base",
          themeVariables: {
            primaryColor: "#ffffff",
            primaryTextColor: "#2c3e50",
            primaryBorderColor: "#3498db",
            lineColor: "#34495e",
          },
          er: {
            fontSize: 12,
            useMaxWidth: true,
            layoutDirection: "TB",
            nodeSpacing: 50,
            rankSpacing: 15,
            entityPadding: 10,
            stroke: "#333333",
          },
          flowchart: {
            nodeSpacing: 50,
            rankSpacing: 15,
            padding: 10,
          },
        });

        //  Stay Folio ERD 
        const diagramDefinition = `
        erDiagram
            %%   
            MEMBER_INFO {
                varchar MI_ID PK "ID"
                varchar MI_PW ""
                varchar MI_NAME ""
                char MI_GENDER ""
                char MI_BIRTH ""
                varchar MI_PHONE ""
                char MI_ISAD ""
                date MI_DATE ""
                char MI_ENABLED ""
            }
            
            MEMBER_ROLE {
                varchar MI_ID FK "ID"
                varchar MR_NAME ""
            }
            
            %%  
            STAY_INFO {
                number SI_ID PK "ID"
                varchar SI_NAME ""
                varchar SI_DESC ""
                varchar SI_LOCA ""
                number LC_ID FK "ID"
                number SI_BOOK ""
                number SI_REVIEW ""
                number SI_MINPERSON ""
                number SI_MAXPERSON ""
                number SI_MINPRICE ""
                number SI_EXTRA ""
                number SI_PEAK ""
                number SI_OFF ""
                number SI_DISCOUNT ""
                char SI_SHOW ""
                char SI_DELETE ""
                date SI_DATE ""
            }
            
            STAY_INFO_DETAIL {
                number SI_ID PK,FK "ID"
                varchar SI_NOTICE ""
                varchar SI_DESC1 "1"
                varchar SI_DESC2 "2"
                varchar SI_FEAT1 "1"
                varchar SI_FEAT2 "2"
                varchar SI_ADDRESS ""
                varchar SI_ADDRDESC ""
                varchar SI_PHONE ""
                varchar SI_EMAIL ""
                varchar SI_INSTAGRAM ""
                varchar SI_BIZNAME ""
                char SI_BIZNUM ""
                varchar SI_CEO ""
                char SI_PET ""
                char SI_PARKING ""
                char SI_FOOD ""
                char SI_CHECKIN ""
                char SI_CHECKOUT ""
                varchar SI_FEAT_TITLE1 "1"
                varchar SI_FEAT_TITLE2 "2"
            }
            
            %%  
            ROOM_INFO {
                number RI_ID PK "ID"
                number SI_ID FK "ID"
                char RI_TYPE ""
                varchar RI_NAME ""
                varchar RI_DESC ""
                number RI_PERSON ""
                number RI_MAXPERSON ""
                number RI_AREA ""
                varchar RI_BED ""
                number RI_BEDCNT ""
                number RI_PRICE ""
                char RI_SHOW ""
                char RI_DELETE ""
                date RI_DATE ""
                number RI_BEDROOM ""
                number RI_BATHROOM ""
            }
            
            %% / 
            FACILITY_INFO {
                number FI_ID PK "ID"
                varchar FI_NAME ""
                varchar FI_ICON ""
            }
            
            AMENITIES_INFO {
                number AI_IDX PK "ID"
                varchar RA_NAME ""
            }
            
            %%  
            LOCATION_CATEGORY {
                number LC_ID PK "ID"
                varchar LC_NAME ""
            }
            
            RECOMMEND_CATEGORY {
                number RC_ID PK "ID"
                varchar RC_NAME ""
                varchar RC_DETAIL_TOP ""
                varchar RC_DETAIL_BOTTOM ""
                varchar RC_ICON ""
            }
            
            %%  
            STAY_PHOTO {
                number SP_IDX PK "ID"
                number SI_ID FK "ID"
                number RI_ID FK "ID"
                varchar SP_URL "URL"
            }
            
            ROOM_PHOTO {
                number SP_IDX PK "ID"
                number SI_ID FK "ID"
                number RI_ID FK "ID"
                varchar SP_URL "URL"
            }
            
            %%  
            STAY_RESERVATION {
                char SR_ID PK "ID"
                number SI_ID FK "ID"
                number RI_ID FK "ID"
                varchar MI_ID FK "ID"
                varchar SR_NAME ""
                varchar SR_EMAIL ""
                varchar SR_PHONE ""
                clob SR_REQUEST ""
                date SR_DATE ""
                number SR_ADULT ""
                number SR_CHILD ""
                date SR_CHECKIN ""
                date SR_CHECKOUT ""
                number SR_ROOMPRICE ""
                number SR_DISCOUNT ""
                number SR_ADDPERSON_FEE ""
                number SR_TOTALPRICE ""
                varchar SR_PAYMENT ""
                date SR_PAYDATE ""
                date SR_CANCLEDATE ""
                varchar SR_STATUS ""
                char SR_PAYMENTSTATUS ""
            }
            
            %%  
            STAY_BOOKMARKS {
                varchar MI_ID FK "ID"
                number SI_ID FK "ID"
                date SB_REGDATE ""
            }
            
            STAY_FACILITY_REL {
                number SI_ID FK "ID"
                number FI_ID FK "ID"
            }
            
            ROOM_FACILITY_REL {
                number SI_ID FK "ID"
                number RI_ID FK "ID"
                number FI_ID FK "ID"
            }
            
            ROOM_AMENITIES {
                number RA_IDX PK "ID"
                number SI_ID FK "ID"
                number RI_ID FK "ID"
                number AI_IDX FK "ID"
            }
            
            STAY_RECOMMEND {
                number SR_IDX PK "ID"
                number SI_ID FK "ID"
                number RC_ID FK "ID"
            }
            
            %%  
            MEMBER_INFO ||--o{ MEMBER_ROLE : ""
            MEMBER_INFO ||--o{ STAY_BOOKMARKS : ""
            MEMBER_INFO ||--o{ STAY_RESERVATION : ""
            
            LOCATION_CATEGORY ||--o{ STAY_INFO : ""
            STAY_INFO ||--|| STAY_INFO_DETAIL : ""
            STAY_INFO ||--o{ ROOM_INFO : ""
            STAY_INFO ||--o{ STAY_PHOTO : ""
            STAY_INFO ||--o{ STAY_BOOKMARKS : ""
            STAY_INFO ||--o{ STAY_FACILITY_REL : ""
            STAY_INFO ||--o{ STAY_RECOMMEND : ""
            STAY_INFO ||--o{ STAY_RESERVATION : ""
            
            ROOM_INFO ||--o{ ROOM_PHOTO : ""
            ROOM_INFO ||--o{ ROOM_FACILITY_REL : ""
            ROOM_INFO ||--o{ ROOM_AMENITIES : ""
            ROOM_INFO ||--o{ STAY_RESERVATION : ""
            
            FACILITY_INFO ||--o{ STAY_FACILITY_REL : ""
            FACILITY_INFO ||--o{ ROOM_FACILITY_REL : ""
            
            AMENITIES_INFO ||--o{ ROOM_AMENITIES : ""
            
            RECOMMEND_CATEGORY ||--o{ STAY_RECOMMEND : ""
        `;

        mermaid
          .render("mermaid-erd-diagram", diagramDefinition)
          .then(({ svg }) => {
            if (mermaidRef.current) {
              mermaidRef.current.innerHTML = svg;

              const svgElement = mermaidRef.current.querySelector("svg");
              if (svgElement) {
                // SVG     
                svgElement.style.width = "100%";
                svgElement.style.height = "100%";
                svgElement.style.minWidth = "auto";
                svgElement.style.minHeight = "auto";
                svgElement.style.maxWidth = "100%";
                svgElement.style.maxHeight = "100%";
                svgElement.style.background = "white";

                // viewBox     
                const bbox = svgElement.getBBox();
                if (bbox.width && bbox.height) {
                  //     
                  const padding = 50;
                  const viewBoxX = bbox.x - padding;
                  const viewBoxY = bbox.y - padding;
                  const viewBoxWidth = bbox.width + padding * 2;
                  const viewBoxHeight = bbox.height + padding * 2;

                  svgElement.setAttribute(
                    "viewBox",
                    `${viewBoxX} ${viewBoxY} ${viewBoxWidth} ${viewBoxHeight}`
                  );
                  svgElement.setAttribute(
                    "preserveAspectRatio",
                    "xMidYMid meet"
                  );

                  // SVG    
                  const existingRect =
                    svgElement.querySelector('rect[fill="white"]');
                  if (!existingRect) {
                    const backgroundRect = document.createElementNS(
                      "http://www.w3.org/2000/svg",
                      "rect"
                    );
                    backgroundRect.setAttribute("x", viewBoxX);
                    backgroundRect.setAttribute("y", viewBoxY);
                    backgroundRect.setAttribute("width", viewBoxWidth);
                    backgroundRect.setAttribute("height", viewBoxHeight);
                    backgroundRect.setAttribute("fill", "white");
                    backgroundRect.setAttribute("stroke", "none");
                    svgElement.insertBefore(
                      backgroundRect,
                      svgElement.firstChild
                    );
                  }
                }

                //    ( )
                const textElements = svgElement.querySelectorAll("text");
                textElements.forEach((text) => {
                  const currentSize = parseFloat(text.style.fontSize) || 12;
                  text.style.fontSize = `${Math.max(currentSize * 1.2, 14)}px`;
                  text.style.fontWeight = "500";
                });
              }
            }
          })
          .catch((error) => {
            console.error("Mermaid rendering failed:", error);
            if (mermaidRef.current) {
              mermaidRef.current.innerHTML = `
              <div style="padding: 20px; text-align: center; color: #e74c3c;">
                <h3> ERD  </h3>
                <p>: ${error.message}</p>
              </div>
            `;
            }
          });
      } catch (error) {
        console.error("Mermaid initialization failed:", error);
        if (mermaidRef.current) {
          mermaidRef.current.innerHTML = `
            <div style="padding: 20px; text-align: center; color: #e74c3c;">
              <h3> Mermaid  </h3>
              <p>: ${error.message}</p>
            </div>
          `;
        }
      }
    }, 100);

    return () => clearTimeout(timer);
  }, []);

  return (
    <div
      ref={mermaidRef}
      style={{
        width: "100%",
        height: "100%",
        minHeight: "100vh",
        overflow: "hidden",
        background: "white",
        padding: "0",
        margin: "0",
        border: "none",
      }}
    ></div>
  );
};

export default MermaidERD;
